import React, { useEffect, useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCalendarCheck, faAngleUp } from '@fortawesome/free-solid-svg-icons';
import { useNavigate } from 'react-router-dom';
import './Hero.css';
import { isMobile } from 'react-device-detect';

function Hero() {
	const navigate = useNavigate();
	const [goUp, setGoUp] = useState(false);

	const scrollToTop = () => {
		window.scrollTo({ top: 0, behavior: 'smooth' });
	};

	const handleBookAppointmentClick = () => {
		navigate('/appointment');
	};

	useEffect(() => {
		const onPageScroll = () => {
			if (window.scrollY > 600) {
				setGoUp(true);
			} else {
				setGoUp(false);
			}
		};
		window.addEventListener('scroll', onPageScroll);

		return () => {
			window.removeEventListener('scroll', onPageScroll);
		};
	}, []);

	return (
		<>
			<div className="section-container" style={{ height: isMobile ? '80vh' : ''}}>
				<div className="hero-section">
					<div style={{ display: isMobile ? 'none' : '', padding: '100px 50px 0px 80px' }}>
						<div className="ad-section-container" />
					</div>
					<div className="text-section" style={{ padding: '160px 80px 0px 0',display:'flex',flexDirection:'column', alignItems: 'flex-end' }}>
						<p className="text-headline">{`Work with us today`}</p>
						<h2 className="text-title" style={{ fontWeight: isMobile ? 'bold' : 'normal', fontSize: isMobile ? '': '50px' }}>{`Impact through simple solutions`}</h2>
						<p className="text-descritpion">{`We listen, understand, and deliver simple effective solutions. With clear requirements and strong collaboration, no solution is too complex. Let our technical expertise transform your business processes.`}</p>

						<button className="text-appointment-btn" type="button" onClick={handleBookAppointmentClick} style={{ margin: '80px 0', width: '50%' }}>
							<FontAwesomeIcon icon={faCalendarCheck} /> {`Contact Us To Find How`}
						</button>


					</div>
				</div>

				<div onClick={scrollToTop} className={`scroll-up ${goUp ? 'show-scroll' : ''}`}>
					<FontAwesomeIcon icon={faAngleUp} />
				</div>
			</div>

			<div className="hero-section" style={{ display: isMobile ? 'none' : '', height: '60vh', backgroundColor: '#FFDEB3' }}>
				<div className="text-section" style={{ padding: '0px 50px 0 50px' }}>
					<h2 className="text-title" style={{ fontWeight: isMobile ? 'bold' : 'normal', fontSize: isMobile ? '': '50px' }}>{`Committed to produce exceptional work`}</h2>
					<p className="text-descritpion">{`We are committed to producing exceptional software for our partners and work closely with our users throughout development to ensure that we are aligned with the end-goal.`}</p>
				</div>
				<div className="text-section" style={{ padding: '0px 50px 0px 50px' }}>



				</div>
			</div>



		</>
	);
}
export default Hero;